@keyframes blink-animation {
  50% {
    opacity: 1;
  }
}

.textA-with-cursor-home::after {
  content: '|';
  color: gray;
  display: inline-block;
  animation-name: blink-animation;
  animation-delay: 0ms;
  animation-duration: 500ms;
  animation-iteration-count: 3;
  opacity: 0;
}

.textB-with-cursor-home::after {
  content: '|';
  color: gray;
  display: inline-block;
  animation-name: blink-animation;
  animation-duration: 500ms;
  animation-delay: 1700ms;
  animation-iteration-count: 3;
  opacity: 0;
}





.rectangle-home {
  overflow: hidden;
  position: absolute;
  bottom: 0px;
  right: 0px;
  //display: inline-block;
  width: 80%;
  height: 100%;
  border: transparent;
  //background: rgba(0, 0, 0, 0.5);
}

.title-container-home {
  position: relative;
  margin: 0 10px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;

  cursor: default;
  z-index: 2;
  //border: solid 2px #222222;


  @media (min-width: 992px) {

    margin-top: 10px;
    margin-left: 0px;
  }
}


@media (max-width: 991px) {


  .header_burger-home {

    display: block;
    position: absolute;
    right: 30px;
    top: 35px;
    width: 30px;
    height: 20px;

    transform: scale(1.12);
    z-index: 3;

  }
  .header_burger-home span {
    position: absolute;
    background-color: rgba(255, 255, 255, 1);
    width: 100%;
    height: 2px;
    left: 0;
    top: 9px;

    transition: all 0.3s ease-in-out 0s;

  }

  .header_burger-home:before,
  .header_burger-home:after {
    content: '';
    background-color: rgba(255, 255, 255, 1);
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;

    transition: all 0.3s ease-in-out 0s;
  }
  .header_burger-home:before {
    top: 0;
  }
  .header_burger-home:after {
    bottom: 0;
  }

  .header_burger-home.active-home span {
    opacity: 0;
  }

  .header_burger-home.active-home:before {
    opacity: 0;

  }
  .header_burger-home.active-home:after {
    opacity: 0;

  }

  .header_burger-home.active-home {
    z-index: 100;
  }
}

