.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

}

.flex-row-center {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.button-style-home {

  display: flex;


  //top: 18px;

  //margin-top: 100px;
  //margin-left: 45px;
  flex-direction: row;
  align-items:center;

  width: 50vw;
  height: 58px;
  border-radius: 10px;

  justify-content: center;

  outline:none;
  text-align: center;

  background: rgba(255, 255, 255, 0.0);
  border: solid 3px var(--color-orange);
  //border: 2px solid transparent;

  letter-spacing:1px;
  color:white;


  font:{
    family: 'ProximaNova-Regular';
    size:5.6vw;
    weight:400;
  }

  cursor: pointer;
  transition: all 0.25s ease;



