.home-planet-animation-container {
  z-index: -1;
  zoom: 140%;

  position: absolute;

  right: 30%;
  top: 20%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);

  @media (min-width: 992px) {
    right: 34%;
    top: 20%;

    zoom: 130%;

  }
}

.home-cargo-animation-container {
  zoom: 90%;
  position: absolute;

  right: -5%;
  width: 150%;

  top: 64%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);

  @media (min-width: 992px) {
    top: 60%;
    zoom: 100%;

  }

}

.flex-row-pc-column-mobile-home {
